import thunk from 'redux-thunk' // redux-thunk  dispatch function
import { createStore, applyMiddleware, compose } from 'redux' // redux createStorecompose

// 
const middleware = [thunk]

// composestore store  applyMiddleware  redux-devtools 
const finalCreateStore = compose(
    applyMiddleware(...middleware),
)(createStore)

export default finalCreateStore


